.navbar {
    background-color: red;
    color: white;
    padding: 20px 0;
    text-align: center;
  }
  
  .navbar h1 {
    margin: 0;
    color: orange;
  }
  
  .container {
    max-width: 400px;
    margin: 20px auto;
    padding: 20px;
    text-align: center;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .loggedOut,
  .loggedIn {
    margin-top: 20px;
  }
  
  button.loginButton {
    background-color: red;
    color: #fff;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    margin-top: 10px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  }
  
  button.loginButton:hover {
    background-color: pink;
  }
  